/* Styling for header */
.header {
    background-color: rgb(104, 3, 3);
    padding: 30px;
    text-align: center;
    font-size: 20px;
    color: white;
}

.sub-header {
    font-size: 10px;
    margin-top: 10px;
    text-decoration: underline;
} 

/* Styling for nav-bar containers */
.nav-container {
    /* background-color: orange; */
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
}

.nav-block {
    /* background-color: red; */
    width: 200px;
    height: 100px;
    margin: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    align-content: center;
}

/* Styling for Nav Buttons and links */

.header button {
    /* font-size: 15px; */
    background-color: orange;
    color: white;
    padding: 15px;
}

.header a.button {
    -webkit-appearance: button;
    -moz-appearance: button;
    appearance: button;

    text-decoration: none;
    background-color: orange;
    padding: 15px;

}


.header a:hover {
    /* background-color: rgba(193, 58, 58, 0.77); */
    -webkit-animation: bounce;
            animation: bounce;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

@-webkit-keyframes bounce {
    30% {
        transform: translateY(10px);
    }
}

@keyframes bounce {
    30% {
        transform: translateY(10px);
    }
}





.clock-body {
    background: linear-gradient(to right, hsl(200,100%, 50%), hsl(175, 100%, 50%));
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 70vh;
    overflow: hidden;
}

.clock {
    width: 300px;
    height: 300px;
    background-color: rgba(216, 231, 241, 0.616);
    border-radius: 50%;
    border: 2px solid white;
    position: relative;
}

.clock .number {
    --rotation: 0; /* sets default to 0 deg*/
    font-size: 1.5rem;
    color: white;
    position: absolute;
    width: 100%;
    height: 100%;
    text-align: center;
    /* background-color: red; */
    transform: rotate(var(--rotation));
    /* line above creates rotation variable for all */
}

.clock .number1 { --rotation: 30deg; }
.clock .number2 { --rotation: 60deg; }
.clock .number3 { --rotation: 90deg; }
.clock .number4 { --rotation: 120deg; }
.clock .number5 { --rotation: 150deg; }
.clock .number6 { --rotation: 180deg; }
.clock .number7 { --rotation: 210deg; }
.clock .number8 { --rotation: 240deg; }
.clock .number9 { --rotation: 270deg; }
.clock .number10 { --rotation: 300deg; }
.clock .number11  { --rotation: 330deg; }

.clock .hand {
    --rotation: 0;
    position: absolute;
    bottom: 50%;
    left: 50%;
    border: 1px solid rgba(255, 255, 255, 0.692);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    transform-origin: bottom;
    z-index: 10;
    transform: translate(-50%) rotate(calc(var(--rotation) * 1deg));
}

.clock::after {
    /* adds a pseudo element we can style */
    content: ""; 
    position: absolute;
    background-color: white;
    z-index: 11;
    width: 15px;
    height: 15px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
}

.clock .hand.second {
    width: 4px;
    height: 45%;
    background-color: red;
}

.clock .hand.minute {
    width: 7px;
    height: 40%;
    background-color: black;
}

.clock .hand.hour {
    width: 10px;
    height: 25%;
    background-color: black;
}


/* Calculator Styling */
.calculator-body {
    padding: 2rem;
    margin: 0;
    background: linear-gradient(to right, #00AAFF, #00FF6c)
}

.calculator-grid {
    display: grid;
    justify-content: center;
    align-content: center;
    min-height: 100vh;
    grid-template-columns: repeat(4, 100px);
    grid-template-rows: minmax(120px, auto) repeat(5, 100px)
}

.calculator-grid > button {
    cursor: pointer;
    font-size: 2rem;
    border: 1px solid white;
    outline: none;
    background-color: rgba(255, 255, 255, .75);
}

.calculator-grid > button:hover {
    background-color: rgba(255, 255, 255, .9)
}

.span-two {
    grid-column: span 2;
}

.output {
    grid-column: 1 / -1;
    background-color: rgba(0,0,0,.75);
    display: flex;
    align-items: flex-end;
    justify-content: space-around;
    flex-direction: column;
    padding: 10px;
    word-wrap: break-word;
    word-break: break-all;
}

.output .previous-operand {
    color: rgba(255, 255, 255, .75);
    font-size: 1.5rem;
}

.output .current-operand {
    color: rgba(255, 255, 255, .75);
    font-size: 2.5rem;
}
